import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import back from '../../assets/img/back.png';
import backblack from '../../assets/img/backblack.png';
import LoginButton from '../../components/LoginButton';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import Input from '../../components/Input';
import { useSelector } from 'react-redux';
import { Spinner } from 'react-bootstrap';
import { fetchSendForgotPasswordToEmail } from '../../store/reducers/auth';

const SendMessageToEmail = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch()
    const {status} = useSelector(state=>state.auth)
    const {loading} = useSelector(state=>state.auth)
    const [email,setEmail] = useState('')


    const emailHandler = (e) =>{
        setEmail(e.target.value)
    }

    const goBack = () => {
        navigate(-1)
    }

    const onSubmit = () =>{
        dispatch(fetchSendForgotPasswordToEmail({email}))
    }
    useEffect(()=>{
        if(status==='200'){
            alert(" ")
            setEmail('')
        }
    },[status])

        
    return (
        <div className='login'>
            <div className='login__body'>
                <div className='login__welcom'>
                <div className='login__back'>
                    <img src={back} alt=' ' onClick={goBack}/>
                </div>
                <div className='login__text'>
                    <h4>
                     <br/> <span>MEBEL</span>
                    </h4>
                    <p>
                      ?<br/>
                         <br/> 
                        <br/> 
                     
                    <span> Mebel</span>
                    </p>
                </div>
                </div>
                <div className='login__inputs'>
                    <div className='back'>
                        <img src={backblack} alt=' ' onClick={goBack}/>
                    </div>
                    <div className="inputs">
                        <h4 className='login__sign'>  </h4>
                        <Input name='email'  type='text' placeholder=' ' value={email} setValue={emailHandler} /> 
                        <p>    ?</p>
                        <Link to='/login'>  </Link>
                        {loading
                            ?<div style={{textAlign:'center'}}><Spinner animation="border" variant="primary" /></div>
                            :<LoginButton title={''} color={'#0058A3'} textColor={'white'} onClick={onSubmit}/>}
                    </div>
                </div>
            </div>
        </div>
  );
};

export default SendMessageToEmail;
