import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import back from '../../assets/img/back.png';
import backblack from '../../assets/img/backblack.png';
import LoginButton from '../../components/LoginButton';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { fetchLogin } from '../../store/reducers/auth';
import useAuth from '../../hooks/useAuth';
import Input from '../../components/Input';
import { useSelector } from 'react-redux';
import { Spinner } from 'react-bootstrap';

const Login = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { status } = useSelector((state) => state.auth);
  const { loading } = useSelector((state) => state.auth);
  const { isAuth } = useAuth();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(null);

  const goBack = () => {
    navigate('/');
  };

  const emailHandler = (e) => {
    setEmail(e.target.value);
  };

  const passswordHandler = (e) => {
    setPassword(e.target.value);
  };

  const onSubmit = () => {
    dispatch(fetchLogin({ email, password }));
  };
  const pushToRegister = () => {
    navigate('/register');
  };
  useEffect(() => {
    if (isAuth) {
      goBack();
    }
  }, [isAuth]);

  useEffect(() => {
    if (status === 'error') {
      setError(' email  ');
    }
  }, [status]);

  return (
    <div className='login'>
      <div className='login__body'>
        <div className='login__welcom'>
          <div className='login__back'>
            <img src={back} alt=' ' onClick={goBack} />
          </div>
          <div className='login__text'>
            <h4>
                
              <br />
               <span>MEBEL</span>
            </h4>
            <p>
                ?
              <br />
                   
              <br />
                  
              <br />
                  
              <br />
               
              <span> Mebel</span>
            </p>
          </div>
        </div>
        <div className='login__inputs'>
          <div className='back'>
            <img src={backblack} alt=' ' onClick={goBack} />
          </div>
          <div className='inputs'>
            <h4 className='login__sign'>  </h4>
            {error && <p style={{ fontSize: 12, margin: 0, color: '#f34336' }}>{error}</p>}
            <Input
              name='email'
              type='text'
              placeholder=' '
              value={email}
              setValue={emailHandler}
            />
            <Input
              name='password'
              type='password'
              placeholder=''
              value={password}
              setValue={passswordHandler}
            />
            <Link to='/forgotPassword'> ?</Link>
            {loading ? (
              <div style={{ textAlign: 'center' }}>
                <Spinner animation='border' variant='primary' />
              </div>
            ) : (
              <LoginButton
                title={''}
                color={'#0058A3'}
                textColor={'white'}
                onClick={onSubmit}
              />
            )}
            <LoginButton
              title={' '}
              color={'#E8E8E8'}
              textColor={'black'}
              onClick={pushToRegister}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
